@observer
class Contestants extends Component {
    render() {
        const {
            currentClass,
            contestants,
            limit,
            onClickMusher,
            tdClass,
            tdStyle,
            progressStyle,
            progressbarStyle,
        } = this.props;

        const { checkpoints = [], results = [] } = currentClass;
        const finalContestants = contestants && limit ? contestants.slice(0, limit) : contestants;

        const { raceStore } = this.props.stores;
        const { sortedContestants = [] } = raceStore;

        const reqTotalRestInSec = currentClass.requiredRestMinutes * 60;
        const now = Math.floor(new Date().getTime() / 1000, 10);

        return (<>
            {finalContestants && finalContestants.map((co, coIdx) => {
                let cpIdx = 0;
                let hasLeftCpPrev;
                let bibIdxOutPrev;
                let estTimeToCpMinutesPrev;
                let hasScratchedPrev;
                let totalInCpTime = 0;

                let selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(co.bib, 10)) : null;
                selectedMusher = selectedMusher ? selectedMusher : {};

                return(<>
                    <tr class='musher' onClick={onClickMusher} data-bib={co.bib}>
                        <td class={tdClass} style={tdStyle}>
                            {co.startTime ? util.isoTime(co.startTime) : ''}
                        </td>

                        {checkpoints && checkpoints.map((cp, idx) => {
                            const checkpointResults = results.slice().filter(e => e.checkpoint === cp.id).sort(fieldSorter(['timestamp']));
                            const res = checkpointResults.filter(e => parseInt(e.bib, 10) == co.bib);
                            const hasLeftCp = res.find(e => e.direction === 'out');
                            const hasArrivedCp = res.find(e => e.direction === 'in');
                            const hasScratched = res.find(e => e.direction === 'scratched');

                            const allResultsIn = checkpointResults.filter(e => (e.direction === 'in' || e.direction === 'scratched'));
                            const bibIdxIn = allResultsIn.findIndex(e => parseInt(e.bib, 10) == co.bib);

                            const allResultsOut = checkpointResults.filter(e => e.direction === 'out');
                            const bibIdxOut = allResultsOut.findIndex(e => parseInt(e.bib, 10) == co.bib);

                            let timeInCp = 0;
                            let percentInCp;
                            if (!cp.isStartLine && hasArrivedCp && hasLeftCp && hasArrivedCp.timestamp && hasLeftCp.timestamp) {
                                timeInCp = mu.deltaSec(hasArrivedCp.timestamp, hasLeftCp.timestamp);
                            }
                            totalInCpTime += timeInCp ? timeInCp : 0;

                            if (!hasLeftCp && hasArrivedCp && !cp.isStartLine) {
                                if (cp.timeEqualization && reqTotalRestInSec) {
                                    const restInCpSoFar = mu.deltaSec(hasArrivedCp.timestamp, now);
                                    // console.log({ reqTotalRestInSec, totalInCpTime, restInCpSoFar })
                                    percentInCp = (totalInCpTime + restInCpSoFar) / reqTotalRestInSec * 100;
                                    percentInCp = percentInCp > 100 ? 100 : percentInCp;
                                    // console.log({ percentInCp })
                                }
                            }

                            if (hasScratchedPrev) {
                                return '';
                            }

                            if (bibIdxIn === -1)  {
                                if (hasLeftCpPrev) {
                                    const nowEpoch = Math.floor(new Date().getTime() / 1000);
                                    const prevCpOutEpoch = Math.floor(new Date(hasLeftCpPrev.timestamp).getTime() / 1000);
                                    const timeUsedOnStage = Math.floor((nowEpoch - prevCpOutEpoch) / 60);
                                    // let percentDone = timeUsedOnStage / estTimeToCpMinutesPrev * 100;
                                    let percentDone = timeUsedOnStage / cp.estTimeToCpMinutes * 100;
                                    percentDone = percentDone > 95 ? 95 : percentDone;
                                    const line = (<>
                                        <td class={`text-center align-middle px-0 pr-1 ${tdClass}`} style={tdStyle}>
                                            <div class='progress' style={`${progressStyle}`}>
                                                <div
                                                    class={`progress-bar progress-bar-striped`}
                                                    role='progressbar'
                                                    style={`width: ${percentDone}%; ${progressbarStyle}`}
                                                    aria-valuenow={percentDone}
                                                    aria-valuemin='0'
                                                    aria-valuemax='100'
                                                    data-prevtimestamp={hasLeftCpPrev.timestamp}
                                                    data-timeUsedOnStage={timeUsedOnStage}
                                                    data-estTimeToCpMinutesPrev={estTimeToCpMinutesPrev}
                                                    data-nowEpoch={nowEpoch}
                                                    data-prevCpOutEpoch={prevCpOutEpoch}
                                                >{bibIdxOutPrev + 1 > 0 ? bibIdxOutPrev + 1 : ''}</div>
                                            </div>

                                            {/* estTimeToCpMinutesPrev: {estTimeToCpMinutesPrev}
                                            timeUsedOnStage: {timeUsedOnStage}
                                            percentDone: {percentDone} */}
                                            {co.cpId && res.length > 0 && <>
                                                {/* <xmp>{JSON.stringify(co, null, 4)}</xmp><br /> */}
                                                {/* <xmp>{JSON.stringify(res, null, 4)}</xmp><br /> */}
                                                {/* <xmp>{JSON.stringify(allResults, null, 4)}</xmp><br /> */}
                                            </>}
                                        </td>
                                    </>);
                                    bibIdxOutPrev = bibIdxOut;
                                    hasLeftCpPrev = hasLeftCp;
                                    estTimeToCpMinutesPrev = cp.estTimeToCpMinutes;
                                    return line;
                                }
                                return '';
                            }
                            cpIdx = idx;
                            bibIdxOutPrev = bibIdxOut;
                            hasLeftCpPrev = hasLeftCp;
                            estTimeToCpMinutesPrev = cp.estTimeToCpMinutes;

                            const output = (<>
                                {(hasArrivedCp || hasScratched) && idx > 0 && <>
                                    <td class={`text-center align-middle px-0 pr-1 ${tdClass}`} style={tdStyle}>
                                        <div class='progress' style={`${progressStyle}`}>
                                            <div
                                                class={`progress-bar`}
                                                role='progressbar'
                                                style={`width: 100%; ${progressbarStyle}`}
                                                aria-valuenow='100'
                                                aria-valuemin='0'
                                                aria-valuemax='100'
                                            />
                                        </div>
                                        {co.cpId && res.length > 0 && <>
                                            {/* <xmp>{JSON.stringify(co, null, 4)}</xmp><br /> */}
                                            {/* <xmp>{JSON.stringify(res, null, 4)}</xmp><br /> */}
                                            {/* <xmp>{JSON.stringify(allResults, null, 4)}</xmp><br /> */}
                                        </>}
                                    </td>
                                </>}
                                <td class={`text-center align-middle px-0 pr-1 ${tdClass}`} style={tdStyle}>
                                    <div class='progress' style={`${progressStyle}`}>
                                        <div
                                            class={`progress-bar ${percentInCp > 0 ? 'progress-bar-striped' : ''} ${hasScratched ? 'bg-danger' : 'bg-success'}`}
                                            role='progressbar'
                                            style={`width: ${percentInCp || 100}%; ${progressbarStyle}`}
                                            aria-valuenow={percentInCp}
                                            aria-valuemin='0'
                                            aria-valuemax='100'
                                        >{hasScratched ? <i class='fas fa-times' /> : <>{bibIdxIn + 1 > 0 ? bibIdxIn + 1 : ''}</>}</div>
                                    </div>
                                    {co.cpId && res.length > 0 && <>
                                        {/* <xmp>{JSON.stringify(co, null, 4)}</xmp><br /> */}
                                        {/* <xmp>{JSON.stringify(res, null, 4)}</xmp><br /> */}
                                        {/* <xmp>{JSON.stringify(allResults, null, 4)}</xmp><br /> */}
                                    </>}
                                </td>
                            </>);

                            hasScratchedPrev = hasScratched;
                            return output;
                        })}
                        <td colspan={checkpoints.length * 2 - cpIdx} class={tdClass} style={tdStyle}>
                            <div class='d-flex flex-nowrap'>
                                <div
                                    class='text-white font-weight-bolder pl-2 pr-1 py-0 rounded-pill-left rounded-none mr-2'
                                    style={`background-color: ${mu.getRankingColor(coIdx + 1)}; width: 45px;`}
                                >
                                    {co.bib}
                                </div>
                                <div>
                                    <nobr>
                                        {co.flag} {co.lastname}
                                    </nobr>
                                </div>
                            </div>
                        </td>
                    </tr>
                </>);
            })}
        </>);
    }
}

