@observer
class RaceLiveResults extends Component {
    componentWillReceiveProps(nextProps) {
        if (this.props.currentClass.id !== nextProps.currentClass.id || this.props.reloadLastEpoch !== nextProps.reloadLastEpoch) {
            const { currentClass } = this.props;
            const { raceStore } = this.props.stores;
            const { checkpoints = [], contestants = [], results = [], sortOrderResults, startDate } = currentClass;
            raceStore.sortContestantsByResult({ contestants, results, checkpoints, sortOrderResults, startDate });
        }
	}

    componentDidMount() {
        const { currentClass } = this.props;
        const { raceStore } = this.props.stores;
        const { checkpoints = [], contestants = [], results = [], sortOrderResults, startDate } = currentClass;
        raceStore.sortContestantsByResult({ contestants, results, checkpoints, sortOrderResults, startDate });
	}

    render() {
        const widthCp = 20;
        const width = 40;
		const {
            race,
            currentClass,
            onClickMusher = () => {},
            onClickCheckpoint = () => {},
            selectedCheckpointId,
            limit = 100,
            tableClass = 'table-striped table-sm',
            thClass,
            thStyle,
            tdClass,
            tdStyle,
            progressStyle = 'height: 20px;',
            progressbarStyle,
        } = this.props;
        const { checkpoints = [], sortOrderResults } = currentClass;
        const { raceStore } = this.props.stores;
        const { sortedContestants = [] } = raceStore;
        const dotColor = '#808080';

		return (<>
            <div class='table-responsive'>
                <table class={`table ${tableClass}`}>
                    <thead>
                        <tr>
                            <th class={thClass} style={thStyle}>
                                <div class='' style={`width: ${width}px;`}>
                                    <i class='fas fa-flag' />
                                </div>
                            </th>

                            <Checkpoints stores={this.props.stores}
                                checkpoints={checkpoints}
                                thClass={thClass}
                                thStyle={thStyle}
                                selectedCheckpointId={selectedCheckpointId}
                                width={width}
                                widthCp={widthCp}
                                onClickCheckpoint={onClickCheckpoint}
                                dotColor={dotColor}
                            />

                            <th class={thClass} style={thStyle}>
                                <div class='' style={`width: ${widthCp}px;`}>
                                    <i class='fas fa-flag-checkered' />
                                </div>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <Contestants
                            stores={this.props.stores}
                            currentClass={currentClass}
                            contestants={sortedContestants}
                            limit={limit}
                            onClickMusher={onClickMusher}
                            tdClass={tdClass}
                            tdStyle={tdStyle}
                            progressStyle={progressStyle}
                            progressbarStyle={progressbarStyle}
                        />
                    </tbody>
                </table>
            </div>
		</>);
	}
}

export default RaceLiveResults;
