																		const currentEpoch = Math.floor(new Date(stage.date).getTime() / 1000);
																		const inDate = new Date(0); // The 0 there is the key, which sets the date to the epoch
																		inDate.setUTCSeconds(currentEpoch + parseInt(stage.duration, 10));
																		const images = stage.images.flat();
