import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';
import linkstate from 'linkstate';
import { toJS } from 'mobx';

import mu from '../../lib/musher-util';

const fieldSorter = (fields) => (a, b) => fields.map(o => {
    let dir = 1;
    if (o[0] === '-') { dir = -1; o=o.substring(1); }
    return a[o] > b[o] ? dir : a[o] < b[o] ? -(dir) : 0;
}).reduce((p, n) => p ? p : n, 0);

@observer
class LastResults extends Component {
    render() {
        const { lastResults = [], currentClass = {}, selectedMusher = {}, selectResult = () => {} } = this.props;
        const { userStore } = this.props.stores;
        const { user = {} } = userStore;
        const { language = 'en' } = user;
        return (<>

            <table class='table table-striped'>
                <thead>
                    <tr>
                        <th style='width: 10%'>Time</th>
                        <th style='width: 5%'>Dir</th>
                        <th style='width: 75%'>Checkpoint</th>
                        <th style='width: 10%'>Dogs</th>
                    </tr>
                </thead>
                <tbody>
                    {lastResults && lastResults.length > 0 && lastResults.map(res => {
                        const currentCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(cp => cp.id === res.checkpoint) : null;
                        const requiredRestMin = res.direction === 'in' && currentCheckpoint.requiredRestMinutes ? (
                            currentCheckpoint.timeEqualization ? selectedMusher.timeEqualization + currentCheckpoint.requiredRestMinutes : currentCheckpoint.requiredRestMinutes
                        ) : null;
                        const firstOutTime = new Date(new Date(res.timestamp).getTime() + requiredRestMin * 60000);
                        return (<>
                            <tr 
                                class='resultLine'
                                data-resultid={res.id}
                                onClick={selectResult}
                            >
                                <td>
                                    <nobr>
                                        <span class='text-muted'><i class='fas fa-clock' /></span> {util.formatDate(res.timestamp, { locale: language, year: '2-digit', month: '2-digit', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                    </nobr>
                                </td>
                                <td>
                                    {res.direction === 'in' && <>
                                        <i class='fas fa-sign-in-alt mr-2 text-success' />
                                    </>}
                                    {res.direction === 'out' && <>
                                        <i class='fas fa-sign-out-alt mr-2 text-info' />
                                    </>}
                                    {res.direction === 'scratched' && <>
                                        <i class='fas fa-times mr-2 text-danger' />
                                    </>}
                                </td>
                                <td>
                                    <span class='text-muted'>{currentCheckpoint.sortOrder}:</span> {currentCheckpoint.name}
                                    {/* {requiredRestMin && <>
                                        <div>
                                            {selectedMusher.timeEqualization ? <>
                                                <span class='text-muted ml-2'>Req rest:</span> {currentCheckpoint.requiredRestMinutes} + {selectedMusher.timeEqualization} min<br />
                                                <span class='text-muted ml-2'>First out:</span> {util.formatDate(firstOutTime, { locale: language, year: '2-digit', month: 'short', hour12: false, hour: '2-digit', minute: '2-digit' })}<br />
                                            </> : <>
                                                <span class='text-muted ml-2'>Req rest:</span> {currentCheckpoint.requiredRestMinutes} min<br />
                                                <span class='text-muted ml-2'>First out:</span> {util.formatDate(firstOutTime, { locale: language, year: '2-digit', month: 'short', hour12: false, hour: '2-digit', minute: '2-digit' })}<br />
                                            </>}
                                        </div>
                                    </>} */}
                                </td>
                                <td>
                                    <nobr>
                                        <span class='text-muted'><i class='fas fa-dog ml-3' /></span> {res.dogs}<br />
                                    </nobr>
                                </td>
                            </tr>
                        </>);
                    })}
                </tbody>
            </table>
        </>);
    }
}

@observer
class RaceLiveAddResult extends Component {
    constructor(props) {
        super(props);
        this.state = {
            result: {},
            message: '',
        };
        this.updateTimer = {};
    }

    addResult = async (e) => {
		const { raceStore, appState } = this.props.stores;
        const { direction, classid } = e.target.closest('button').dataset;
        const { checkpointId, selectedClassIds = [] } = appState;

        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);
        const selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === checkpointId) : null;

        const { result, showTimestamp } = this.state;
		const { race } = raceStore;
        const raceid = race.id;

        if (showTimestamp) {
            result.timestamp = result.timestamp ? result.timestamp : new Date();
        } else {
            result.timestamp = new Date();
        }

        if (direction === 'scratched') {
            await raceStore.addResult({
                id: raceid,
                classId: classid,
                showTimestamp: false,
                newResult: {
                    direction: 'in',
                    checkpoint: checkpointId,
                    ...result,
                }
            });
            await raceStore.editContestant({
                id: raceid,
                classId: classid,
                bib: result.bib,
                field: 'raceStatus',
                value: 'DNF',
            });
        }

		await raceStore.addResult({
			id: raceid,
			classId: classid,
            showTimestamp: false,
			newResult: {
                direction,
                checkpoint: checkpointId,
                ...result,
			}
        });

        // const { checkpoints = [], contestants = [], results = [] } = currentClass;
        // raceStore.sortContestantsByResult({ contestants, results, checkpoints });

        this.setState({
            result: {
                bib: '',
                dogs: '',
                timestamp: '',
            },
            selectedMusher: null,
            lastResults: null,
            message: `Added ${result.bib} with ${result.dogs} dogs ${direction} ${direction === 'in' ? 'to' : 'of'} checkpoint ${selectedCheckpoint.name}.`,
        });

        clearTimeout(this.updateTimer.message);
        this.updateTimer.message = setTimeout(async () => {
			this.setState({
                message: '',
            });
		}, 3000);
	}

    editResult = (e) => {
        const { raceStore, appState } = this.props.stores;
        const { currentResult } = this.state;
		const { race } = raceStore;
        const raceid = race.id;

        const field = e.target.id;
        const value = e.target.value;
        const { classid, resultid } = e.target.dataset;

        // const resultIdx = lastResults.findIndex(e => e.id === resultid);
        // if (resultIdx > -1) {
        //     console.log({ resultIdx })
        //     lastResults[resultIdx][field] = value;
        //     this.setState({ lastResults });
        // }
        currentResult[field] = value;
        this.setState({ currentResult });

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        
        const data = {
            raceClassId: classid,
            resultId: resultid,
			[`raceClassResult${util.ucfirst(field)}`]: value,
		};
		clearTimeout(this.updateTimer[field]);
		this.updateTimer[field] = setTimeout(async () => {
            await raceStore.editClass({
                id: raceid,
				data,
			});
            raceStore.updateClassesResultLocal(classid, resultid, field, value);
            const currentClass = raceStore.getClassById(selectedClassId);

            const { checkpoints = [], contestants = [], results = [] } = currentClass;
            raceStore.sortContestantsByResult({ contestants, results, checkpoints });
		}, 500);
    }

    updateField = (e) => {
        const { raceStore, appState } = this.props.stores;
		const { race } = raceStore;
        const raceid = race.id;

        const field = e.target.id;
        const value = e.target.value;
        const { classid, resultid } = e.target.dataset;

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);

        const { result, selectedResult, showTimestamp } = this.state;
        result[field] = value;
        
        const { checkpointId } = appState;
        const { sortedContestants = [] } = raceStore;
        const selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === checkpointId) : null;
        
        const selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(result.bib, 10)) : null;
        if (!selectedMusher) {
            this.setState({
                result,
            });
            return false;
        }
        let lastResults;
        let isCheckedIn;
        let isCheckedOut;
        let isScratched;
        let currentResult;
        let prevCheckpoint;
        let isCheckedInPrev;
        let isCheckedOutPrev;
        let isScratchedPrev;

        if (currentClass) {
            lastResults = currentClass.results ? currentClass.results.slice().sort(fieldSorter(['-timestamp', '-direction'])).filter(e => parseInt(e.bib, 10) === parseInt(result.bib, 10)) : null;
            if (lastResults && lastResults.length > 0) {
                isCheckedIn = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'in');
                isCheckedOut = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'out');
                isScratched = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'scratched');

                prevCheckpoint = raceStore.getPreviousCheckpoint({ currentClass, checkpointId });
                if (prevCheckpoint) {
                    isCheckedInPrev = lastResults.find(e => e.checkpoint === prevCheckpoint.id && e.direction === 'in');
                    isCheckedOutPrev = lastResults.find(e => e.checkpoint === prevCheckpoint.id && e.direction === 'out');
                    isScratchedPrev = lastResults.find(e => e.checkpoint === prevCheckpoint.id && e.direction === 'scratched');
                }
            }
            currentResult = currentClass.results ? toJS(currentClass.results.slice().find(e => e.id === selectedResult)) : {};
        }

        const lastResult = lastResults && lastResults[0] ? lastResults[0] : null;
        if (lastResult) {
            const lastTimestamp = lastResults && lastResults[0] ? lastResults[0].timestamp : (selectedMusher && selectedMusher.startTime ? selectedMusher.startTime : currentClass.startDate);
            if (showTimestamp) {
                result.timestamp = util.isoDate(lastTimestamp, false, false, false);
            }
            result.dogs = lastResult.dogs;
        }
        
        this.setState({
            result,
            selectedCheckpoint,
            selectedMusher,
            isCheckedIn,
            isCheckedOut,
            isScratched,
            isCheckedInPrev,
            isCheckedOutPrev,
            isScratchedPrev,
            prevCheckpoint,
            currentResult,
            lastResults,
        });
    }


    deleteResult = async () => {
        const { selectedResult } = this.state;
        const { appState, raceStore } = this.props.stores;

        const { race } = raceStore;
        const raceid = race.id;

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);

        const data = {
			raceClassId: currentClass.id,
            resultId: selectedResult,
			raceClassResultDelete: 1,
		};
        await raceStore.editClass({
            id: raceid,
            data,
        });

        const { checkpoints = [], contestants = [], results = [] } = currentClass;
        raceStore.sortContestantsByResult({ contestants, results, checkpoints });

        this.doneEditResult();
    }

    selectCheckpoint = (e) => {
        const { showTimestamp, result } = this.state;
        const checkpointId = e.target.closest('button').dataset.id;
        const { appState, raceStore } = this.props.stores;
        appState.selectCheckpoint(checkpointId);

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);
        const selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === checkpointId) : null;

        const { sortedContestants = [] } = raceStore;
        const selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(result.bib, 10)) : null;
        const lastResults = currentClass.results ? currentClass.results.slice().sort(fieldSorter(['-timestamp', '-direction'])).filter(e => parseInt(e.bib, 10) === parseInt(result.bib, 10)) : null;

        const isCheckedIn = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'in');
        const isCheckedOut = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'out');
        const isScratched = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'scratched');

        const prevCheckpoint = raceStore.getPreviousCheckpoint({ currentClass, checkpointId });
        const isCheckedInPrev = lastResults.find(e => e.checkpoint === prevCheckpoint.id && e.direction === 'in');
        const isCheckedOutPrev = lastResults.find(e => e.checkpoint === prevCheckpoint.id && e.direction === 'out');
        const isScratchedPrev = lastResults.find(e => e.checkpoint === prevCheckpoint.id && e.direction === 'scratched');
        
        this.setState({
            selectedCheckpoint,
            selectedMusher,
            isCheckedIn,
            isCheckedOut,
            isScratched,
            isCheckedInPrev,
            isCheckedOutPrev,
            isScratchedPrev,
            lastResults,
        });

    }

    toggleTimestamp = () => {
        const { showTimestamp, result } = this.state;

        const { appState, raceStore } = this.props.stores;
        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);
        const selectedMusher = currentClass.contestants ? currentClass.contestants.find(e => e.bib === parseInt(result.bib, 10)) : null;

        const lastResults = currentClass.results ? currentClass.results.slice().sort(fieldSorter(['timestamp'])).reverse().filter(e => parseInt(e.bib, 10) === parseInt(result.bib, 10)) : null;
        let lastTimestamp;
        if (lastResults && lastResults[0]) {
            lastTimestamp = lastResults[0].timestamp;
        } else if (selectedMusher.startTime) {
            lastTimestamp = selectedMusher.startTime;
        } else if (currentClass.startDate) {
            lastTimestamp = currentClass.startDate;
        } else {
            lastTimestamp = new Date();
        }
        result.timestamp = util.isoDate(lastTimestamp, false, false, false);

        this.setState({
            showTimestamp: !showTimestamp,
            result,
        });
    }

    doneEditResult = () => {
        const { raceStore, appState } = this.props.stores;
        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);
        const { checkpointId } = appState;
        const selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === checkpointId) : null;

        this.setState({
            selectedResult: '',
            selectedCheckpoint,
            currentResult: null,
            result: {
                bib: '',
                dogs: '',
                timestamp: '',
            },
        });
    }

    selectResult = (e) => {
        const resultid = e.target.closest('.resultLine').dataset.resultid;
        
        const { appState, raceStore } = this.props.stores;
        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);
        const currentResult = currentClass.results ? toJS(currentClass.results.slice().find(e => e.id === resultid)) : {};
        
        this.setState({
            selectedResult: resultid,
            currentResult,
        });
    }

    componentWillReceiveProps(nextProps) {
        // if (this.props.currentClass.id !== nextProps.currentClass.id || this.props.reloadLastEpoch !== nextProps.reloadLastEpoch) {
        //     const { currentClass } = this.props;
        //     const { raceStore } = this.props.stores;
        //     const { checkpoints = [], contestants = [], results = [] } = currentClass;
        //     raceStore.sortContestantsByResult({ contestants, results, checkpoints });
        // }
	}

    componentDidMount() {
        this.doneEditResult();
	}

    render() {
        const {
            result,
            showTimestamp,
            message,
            selectedCheckpoint,
            selectedMusher,
            isCheckedIn,
            isCheckedOut,
            isScratched,
            isCheckedInPrev,
            isCheckedOutPrev,
            isScratchedPrev,
            currentResult,
            lastResults = [],
        } = this.state;
        const { appState, raceStore, userStore } = this.props.stores;
        const { checkpointId } = appState;

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);
        const lastResult = lastResults && lastResults[0] ? lastResults[0] : {};
        let lastCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(cp => cp.id === lastResult.checkpoint) : null;
        lastCheckpoint = lastCheckpoint ? lastCheckpoint : {};

        // const selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === checkpointId) : null;

        // const selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(result.bib, 10)) : null;
        // const lastResults = currentClass.results ? currentClass.results.slice().sort(fieldSorter(['-timestamp', '-direction'])).filter(e => parseInt(e.bib, 10) === parseInt(result.bib, 10)) : null;
        // const isCheckedIn = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'in');
        // const isCheckedOut = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'out');
        // const isScratched = lastResults.find(e => e.checkpoint === checkpointId && e.direction === 'scratched');
        // const currentResult = currentClass.results ? currentClass.results.find(e => e.id === selectedResult) : {};

        const { user = {} } = userStore;
        const { language = 'en' } = user;

        return (<>
            <div class='mt-2 pb-5' style='height: 70vh;'>
                <h3>Admin timing - {currentClass.title}</h3>
                <div class='w-100 overflow-hidden'>
                    <div
                        class='d-flex flex-row flex-nowrap pb-2 px-1'
                        style='overflow: auto; scroll-snap-type: x mandatory;'
                    >

                        {currentClass.checkpoints && currentClass.checkpoints.map((currentCheckpoint, idx) => {
                            let resIn = {};
                            let resOut = {};
                            let resScratched = {};
                            if (lastResults && lastResults.length > 0) {
                                resIn = lastResults.slice().find(e => parseInt(e.bib, 10) == selectedMusher.bib && e.checkpoint === currentCheckpoint.id && e.direction === 'in') || {};
                                resOut = lastResults.slice().find(e => parseInt(e.bib, 10) == selectedMusher.bib && e.checkpoint === currentCheckpoint.id && e.direction === 'out') || {};
                                resScratched = lastResults.slice().find(e => parseInt(e.bib, 10) == selectedMusher.bib && e.checkpoint === currentCheckpoint.id && e.direction === 'scratched') || {};
                            }
                            
                            return (<>
                                <div class='col px-0 pt-2'>
                                    <button
                                        type='button'
                                        class={`btn btn-block ${idx === 0 && 'rounded-pill-left'} ${idx === currentClass.checkpoints.length - 1 && 'rounded-pill-right'} rounded-none ${checkpointId === currentCheckpoint.id ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                        style='text-overflow: ellipsis; overflow: hidden;'
                                        onClick={this.selectCheckpoint}
                                        data-id={currentCheckpoint.id}
                                    >
                                        {currentCheckpoint.sortOrder}: {currentCheckpoint.name}
                                        {resOut && resOut.id ? <i class='fas fa-check text-white ml-2' /> : ''}
                                        {resIn && resIn.id && !resOut.id ? <i class='fas fa-map-marker-alt text-white ml-2' /> : ''}
                                    </button>
                                </div>
                            </>);
                        })}

                    </div>
                </div>
                <div class='w-100 overflow-hidden'>
                    {selectedCheckpoint && <>
                        <h3>
                            {selectedMusher && <><i class='fas fa-user text-muted' /> {selectedMusher.firstname} {selectedMusher.lastname}</>}
                        </h3>

                        {currentResult ? <>
                            {['bib', 'dogs'].map(field => <>
                                <div class='form-group'>
                                    <label for={field}>{field}</label>
                                    <input type='text' class='form-control' id={field} aria-describedby={`${field}Help`}
                                        data-classid={currentClass.id}
                                        data-resultid={currentResult.id}
                                        data-field={field}
                                        value={currentResult[field]}
                                        onInput={this.editResult}
                                    />
                                </div>
                            </>)}

                            {['timestamp'].map(field => <>
                                <div class='form-group'>
                                    <label for={field}><i class='fas fa-stopwatch' /> {field}</label>
                                    <input type='datetime-local' class='form-control' id={field} aria-describedby={`${field}Help`}
                                        data-classid={currentClass.id}
                                        data-resultid={currentResult.id}
                                        data-field={field}
                                        value={util.isoDate(currentResult[field], false, false, false)}
                                        onInput={this.editResult}
                                    />
                                    <button class='btn btn-link' onClick={this.toggleTimestamp}>Hide timestamp</button>
                                </div>
                            </>)}

                            <button class='btn btn-lg btn-danger float-left' onClick={this.deleteResult}>
                                Delete
                            </button>
                            <button class='btn btn-lg btn-info float-right' onClick={this.doneEditResult}>
                                Done
                            </button>
                        </> : <>

                            {selectedCheckpoint.sortOrder > lastCheckpoint.sortOrder + 1 && <>
                                <div class='p-3 mb-2 bg-danger text-white clearfix'>
                                    <span class='float-left' style='font-size: 2.0em;'><i class='fas fa-exclamation-triangle mr-3' /></span>
                                    Previous checkpoint is not logged yet.
                                </div>
                            </>}

                            {!isCheckedOutPrev && selectedCheckpoint.sortOrder === lastCheckpoint.sortOrder + 1 && <>
                                <div class='p-3 mb-2 bg-danger text-white clearfix'>
                                    <span class='float-left' style='font-size: 2.0em;'><i class='fas fa-exclamation-triangle mr-3' /></span>
                                    Not checked out of prev.
                                </div>
                            </>}
                            {/* {selectedCheckpoint.sortOrder} gt
                            {lastCheckpoint.sortOrder}<br />
                            isCheckedIn: {JSON.stringify(isCheckedIn, null, 4)}<br />
                            isCheckedOut: {JSON.stringify(isCheckedOut, null, 4)}<br />
                            isScratched: {JSON.stringify(isScratched, null, 4)}<br /> */}

                            {/* bib: String,
                            position: [],
                            checkpointIn: String,
                            checkpointOut: String,
                            dogs: Number,
                            timestamp: Date,
                            addedByUser: Number, */}
                            {['bib'].map(field => <>
                                <div class='form-group'>
                                    <label for={field}>{field}</label>
                                    <input type='text' class='form-control form-control-lg' id={field} aria-describedby={`${field}Help`}
                                        onInput={this.updateField}
                                        value={result[field]}
                                    />
                                </div>
                            </>)}

                            {['dogs'].map(field => <>
                                <div class='form-group'>
                                    <label for={field}><i class='fas fa-dog' /> {field}</label>
                                    <input type='text' class='form-control form-control-lg' id={field} aria-describedby={`${field}Help`}
                                        onInput={linkstate(this, `result.${field}`)}
                                        value={result[field]}
                                    />
                                </div>
                            </>)}

                            {showTimestamp ? <>
                                {['timestamp'].map(field => <>
                                    <div class='form-group'>
                                        <label for={field}><i class='fas fa-stopwatch' /> {field}</label>
                                        <input
                                            type='datetime-local'
                                            class='form-control'
                                            id={field}
                                            aria-describedby={`${field}Help`}
                                            onInput={linkstate(this, `result.${field}`)}
                                            value={util.isoDate(result[field], false, false, false).replace(/ /g, 'T')}
                                            min={util.isoDate(lastResult.timestamp, false, false, false).replace(/ /g, 'T')}
                                        />
                                        <button class='btn btn-link' onClick={this.toggleTimestamp}><i class='fas fa-stopwatch' /> Hide timestamp</button>
                                    </div>
                                </>)}
                            </> : <>
                                <div class='form-group'>
                                    <button class='btn btn-link' onClick={this.toggleTimestamp}><i class='fas fa-stopwatch' /> Show timestamp</button>
                                </div>
                            </>}


                            {isCheckedIn && isCheckedOut && <>
                                <div class='alert alert-warning my-2 clearfix' role='alert'>
                                    <span class='float-left' style='font-size: 2.0em;'><i class='fas fa-exclamation-triangle mr-3' /></span>
                                    {selectedMusher && <>{selectedMusher.firstname} {selectedMusher.lastname}</>} Has checked in and out of this checkpoint!
                                </div>
                            </>}

                            <div class='d-flex justify-content-between'>
                                <div class='text-center'>
                                    <h3><i class='fas fa-campground text-muted' /> {selectedCheckpoint.name}</h3>
                                </div>
                            </div>

                            <div class='d-flex justify-content-between'>
                                <button class='btn btn-lg btn-success float-left' onClick={this.addResult} data-direction='in' data-classid={currentClass.id} disabled={isCheckedIn ? 'disabled' : ''}>
                                    CP In <i class='fas fa-sign-in-alt ml-2' />
                                </button>
                                <button class='btn btn-lg btn-info float-right' onClick={this.addResult} data-direction='out' data-classid={currentClass.id} disabled={isCheckedOut ? 'disabled' : ''}>
                                    <i class='fas fa-sign-out-alt mr-2' /> CP Out
                                </button>
                            </div>
                        </>}

                    </>}
                </div>
                {message && <div class='alert alert-success mt-3 clearfix' role='alert'>
                    {message}
                </div>}

                <div class='overflow-hidden mb-5 mt-3'>
                    <LastResults
                        stores={this.props.stores}
                        lastResults={lastResults}
                        currentClass={currentClass}
                        selectedMusher={selectedMusher}
                        selectResult={this.selectResult}
                    />
                </div>

                <div class='d-flex justify-content-center mt-3 mb-5'>
                    <button class='btn btn-sm btn-block btn-danger' onClick={this.addResult} data-direction='scratched' data-classid={currentClass.id} disabled={isScratched ? 'disabled' : ''}>
                        Scratch <i class='fas fa-times ml-2' />
                    </button>
                </div>

            </div>
        </>);
    }
}

export default RaceLiveAddResult;
