import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

import DogScroller from '../dog/scroller';
import ImageScroller from '../imageScroller';
import RaceTrackingMusherDetail from '../public/raceTrackingMusherDetail';

@observer
class MusherInfoHeader extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    toggleAdminInfo = (e) => {
        e.stopPropagation();
        const { showAdminInfo } = this.state;
        this.setState({
            showAdminInfo: !showAdminInfo,
        });
    }

    toggleInfo = (e) => {
        e.stopPropagation();
        const { showInfo } = this.state;
        this.setState({
            showInfo: !showInfo,
        });
    }

    onDogClick = (e, dogid, dogs) => {
        e.preventDefault();
        e.stopPropagation();
        const currentDog = dogs.find(e => e.uuidv4 === dogid);
        this.setState({
            currentDog,
            showDog: dogid,
            dogs,
        });
        return false;
    }

    chooseSubmenu = (e) => {
		const submenu = e.target.closest('button').dataset.submenu;
		this.setState({ submenu });
	}

    render() {
        const { raceStore, userStore } = this.props.stores;
        const { showAdminInfo, showInfo, submenu = 'details', showDog, currentDog: dog = {} } = this.state;

        const { waypoints, totalTrackLength } = raceStore;

        const { contestant = {}, onMarkerClick = () => {}, children, rank } = this.props;
        const wp = raceStore.findWaypointByBib(contestant.bib);

        const { isAdmin } = userStore;
        const musher = (contestant.bib ? raceStore.getMusherByBibId(contestant.bib) : raceStore.getMusherByBibEmail(contestant.email)) || {};
        const team = raceStore.getTeamById(musher.team);
        const user = team.members ? team.members.find(m => m.email === musher.email) : {};
        const raceClass = raceStore.getClassByBib(contestant.bib);
        const totals = raceStore.getTotalsByTeamId(musher.team);
        let image = mu.getImage({ user, team, priority: 'user', fallback: musher.image1 });
        const shortname = mu.displayNameShort(user) || mu.displayNameShort(musher);

        const dotColor = contestant.color;

        const hasBirthday = mu.hasBirthday(dog.birth);
        const daysUntilBirthday = dog.birth ? Math.ceil(mu.daysUntilNextBirthday(dog.birth)) : 365;

        const totalDistancePercent = util.format(wp.totalDistanceOnTrack / totalTrackLength * 100, 0);
        const { inFront, behind } = raceStore.findNearestMushers(contestant.bib);

        const defaultImage = {
            s3XXLargeLink: musher.topImage1 || './assets/homerun-race-logo.png',
        };
        const headerImage = team && team.imageHeader && team.imageHeader.s3XXLargeLink ? team.imageHeader : defaultImage;

        const teamImages = team && team.images ? [...team.images] : [];
        for (let i = 0, l = 3; i < l; i += 1) {
            if (musher[`image${i}`]) {
                teamImages.push(
                    {
                        s3SmallLink: musher[`image${i}`],
                        s3LargeLink: musher[`image${i}`],
                    }
                );
            }
        }
        return (<>
            <div class='w-100 position-absolute'
                style={`z-index: -1; height: 18vh; top: 0px; left: 0px; background-size: cover; background-repeat: no-repeat; background-position: 50% 30%; background-color: #a0a0a0; ${headerImage ? `background-image: url("${headerImage.s3XXLargeLink}");` : ''}`}
            />

            <div class='d-flex position-relative' style='margin-top: 17vh;'>
                <div
                    class={`position-relative text-center border rounded-circle imageRounded`}
                    style={`margin-top: -50px; ${image ? `background-image: url("${image}"); background-size: cover;` : 'background-color: #c0c0c0;'}`}
                >
                    {!image && <div class='text-muted' style='font-size: 35px; padding-top: 10px;'>{shortname}</div>}
                    <div
                        class={`rounded-pill-both px-2 text-center position-absolute ${dotColor ? '' : 'bg-secondary'} font-weight-bold`}
                        style={`bottom: -8px; right: 0; font-size: 1.2em; ${dotColor ? `background-color: ${dotColor};` : ''}`}
                    >
                        {/* {musher.bib && musher.bib !== 'n/a' && <>{musher.bib}</>} */}
                        {rank && <>{rank}</>}
                    </div>
                </div>
                <div class='flex-grow-1 pl-3 text-overflow mb-0' style='line-height: 1.2em;'>
                    <h5 class='mb-0'>
                        <div class='d-flex flex-nowrap'>
                            <div
                                class='text-white font-weight-bold pl-2 pr-1 py-0 rounded-pill-left rounded-none mr-2'
                                style={`background-color: ${mu.getRankingColor(rank)}; width: 55px;`}
                            >
                                {musher.bib}
                            </div>
                            <div>
                                <nobr>
                                    {musher.flag} {musher.firstname} {musher.lastname}
                                </nobr>
                            </div>
                        </div>
                    </h5>
                    <span class='mb-0' style='margin-left: 93px;'>{musher.sportsClub || team.name || <>&nbsp;</>}</span>
                </div>
            </div>

            <div class='d-flex justify-content-center mb-3'>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-pill-left rounded-none ${submenu === 'details' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='details'
                    >
                        Details
                    </button>
                </div>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-none ${submenu === 'gpsdata' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='gpsdata'
                    >
                        GPS Data
                    </button>
                </div>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-none ${submenu === 'team' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='team'
                    >
                        Team
                    </button>
                </div>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-pill-right rounded-none ${submenu === 'dogs' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='dogs'
                    >
                        Dogs
                    </button>
                </div>
            </div>

            <div
                class='w-100 overflow-auto position-relative'
                // style='max-height: 60vh;'
            >
                {submenu === 'details' && <>
                    {children}
                </>}

                {submenu === 'gpsdata' && <>
                    <RaceTrackingMusherDetail
                        stores={this.props.stores}
                        wp={wp}
                        musher={musher}
                    />
                </>}

                {submenu === 'team' && <>
                    {team.public > 0 ? <>
                        <h3>{team.name}</h3>
                        <Markdown markdown={mu.replaceImages(team.description, team.images)} markdownOpts={mu.getMarkdownOptions()} />
                        <ImageScroller stores={this.props.stores} images={teamImages} />

                    </> : <>
                        <div class='text-center text-muted'>
                            <div class='display-1'><i class='fas fa-dog' /></div>
                            <h3><Text id='teams.no-public-team'>No public team!</Text></h3>
                            <Text id='teams.no-public-team'>This team is not publicly visible.</Text>
                            {teamImages && <div class='my-3'><ImageScroller stores={this.props.stores} images={teamImages} /></div>}
                        </div>
                    </>}
                </>}

                {submenu === 'dogs' && <>
                    {team.public > 0 ? <>
                        {team.dogs && <div class='w-100 container-fluid'>
                            <div class='row pt-2'>
                                <DogScroller stores={this.props.stores} dogs={team.dogs.map(d => d.id)} allDogs={team.dogs} onDogClick={this.onDogClick} />
                            </div>
                        </div>}
                        {showDog && <>                    
                            <div class='d-flex mb-0 mt-3 position-relative'>
                                <div class={`text-center border rounded-circle imageRounded imageRoundedLarge text-muted pt-2`} style={dog.image ? `background-image: url("${dog.image.s3LargeLink}"); background-size: cover;` : ''}>
                                    {!dog.image && <span class='font-weight-lighter' style='font-size: 35px;'>{util.ucfirst(dog.name, true)}</span>}
                                </div>
                                <div class='flex-grow-1 pl-3 pr-2'
                                    style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important; line-height: 1.2em;`}
                                >
                                    <h5 class='mb-1'>
                                        {dog.shortname || dog.name} {dog.shortname && dog.shortname !== '' && <> - <span class='font-weight-lighter'>{dog.name}</span></>}
                                        {hasBirthday && <>
                                            <i class='fas fa-birthday-cake ml-3' />
                                        </>}
                                    </h5>
                                    <div>
                                        {daysUntilBirthday < 7 && <>
                                            <span class='text-muted mr-2'>
                                                <i class='fas fa-birthday-cake' /> in {daysUntilBirthday} days
                                            </span>
                                        </>}                                
                                        {dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
                                        {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                                    </div>
                                </div>
                            </div>
                            {/* <xmp>dog: {JSON.stringify(dog, null, 4)}</xmp> */}
                        </>}
                    </> : <>
                        <div class='text-center text-muted'>
                            <div class='display-1'><i class='fas fa-dog' /></div>
                            <h3><Text id='teams.no-public-team'>No public team!</Text></h3>
                            <Text id='teams.no-public-team'>This team is not publicly visible.</Text>
                        </div>
                    </>}
                </>}

                {isAdmin && <div style='max-height: 200px; max-width: 70vw; overflow: auto;'>
                    <button class='btn btn-link btn-block' onClick={this.toggleAdminInfo}>
                        Toggle admin info
                    </button>
                    {showAdminInfo && <>
                        <small>
                            <xmp>
                                musher: {JSON.stringify(musher, null, 4)}<br />
                                user: {JSON.stringify(user, null, 4)}<br />
                                team: {JSON.stringify(team, null, 4)}<br />
                                totals: {JSON.stringify(totals, null, 4)}<br />

                                {/* {
                                    "firstDate": "2021-03-19 11:59",
                                    "lastDate": "2021-03-20 17:13",
                                    "firstEpoch": 1616151540000,
                                    "lastEpoch": 1616256780000,
                                    "now": {
                                        "$numberDecimal": "1642675856939"
                                    },
                                    "totalTimeUsedSec": 26524316,
                                    "totalWorkoutTimeUsedSec": 130895,
                                    "lastDuration": 25655,
                                    "team": 84,
                                    "count": 4,
                                    "distanceKm": 301.5906237951766,
                                    "elevation": 3610.019075719202,
                                    "duration": 77343,
                                    "rest": 1332,
                                    "speedAvg": 8.294634979660305,
                                    "totalRest": 54884,
                                    "loadIndex": 1080.75,
                                    "totalKcalRelated": 544.535560976128,
                                    "weightedTotalKcal": 12303.171406247127,
                                    "flatTotalKcal": 9424.70699359927,
                                    "firstGpxStartEpoch": null,
                                    "lastGpxStartEpoch": null
                                } */}

                            </xmp>
                        </small>
                    </>}
                </div>}
                <div class='d-flex position-absolute' style='right: 10px; bottom: 10px;'>
                    {inFront.bib && <a class='text-primary mx-2' onClick={() => onMarkerClick(inFront)} style='line-height: 1.0em; font-size: 2.3em;'>
                        <i class='fas fa-angle-left' />
                    </a>}
                    {behind.bib && <a class='text-primary mx-2' onClick={() => onMarkerClick(behind)} style='line-height: 1.0em; font-size: 2.3em;'>
                        <i class='fas fa-angle-right' />
                    </a>}
                </div>
            </div>
        </>);
    }
}

export default MusherInfoHeader;
