import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

import MusherInfoHeader from '../races/musherInfoHeader';

const fieldSorter = (fields) => (a, b) => fields.map(o => {
    let dir = 1;
    if (o[0] === '-') { dir = -1; o=o.substring(1); }
    return a[o] > b[o] ? dir : a[o] < b[o] ? -(dir) : 0;
}).reduce((p, n) => p ? p : n, 0);

@observer
class RaceLiveMusherResult extends Component {
    render() {
        const { bib, onClickNav = () => {} } = this.props;

        const { appState, raceStore, userStore } = this.props.stores;
        const { user: currentUser = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = currentUser;
        const { checkpointId } = appState;
        const { sortedContestants = [] } = raceStore;

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId) || {};

        const { checkpoints = [], contestants = [], results = [] } = currentClass;

        const selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === checkpointId) : null;
        let selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(bib, 10)) : null;
        selectedMusher = selectedMusher ? selectedMusher : {};
        const selectedMusherIdx = sortedContestants ? sortedContestants.findIndex(e => e.bib === parseInt(bib, 10)) : null;
        const lastResults = currentClass.results ? currentClass.results.slice().sort(fieldSorter(['-timestamp', '-direction'])).filter(e => parseInt(e.bib, 10) === parseInt(bib, 10)) : null;

        const team = raceStore.getTeamById(selectedMusher.team);
        const user = team.members ? team.members.find(m => m.email === selectedMusher.email) : {};
        const image = mu.getImage({ user, team, priority: 'user' });
        const shortname = mu.displayNameShort(user) || mu.displayNameShort(selectedMusher);

        let lastResOut;
        let lastResIn;
        let totalDistance = 0;
        let totalTime = 0;
        let totalMovingTime = 0;
        let totalInCpTime = 0;
        let lastEstTimeOut = currentClass.startDate;
        let lastAvgSpeed = 0;

        const nextBib = selectedMusherIdx > -1 && sortedContestants[selectedMusherIdx + 1] ? sortedContestants[selectedMusherIdx + 1].bib : null;
        const prevBib = selectedMusherIdx > -1 && sortedContestants[selectedMusherIdx - 1] ? sortedContestants[selectedMusherIdx - 1].bib : null;

        const locale = 'en-GB';

        return (<>
            {nextBib && <button class='btn btn-link text-primary float-right mr-2 py-0' style='font-size: 2.0em; line-height: 1.0em;' onClick={() => onClickNav(nextBib)}>
                <i class='fas fa-angle-right' />
            </button>}
            {prevBib && <button class='btn btn-link text-primary float-right mr-2 py-0' style='font-size: 2.0em; line-height: 1.0em;' onClick={() => onClickNav(prevBib)}>
                <i class='fas fa-angle-left' />
            </button>}

            <MusherInfoHeader
                stores={this.props.stores}
                contestant={selectedMusher}
                onTeamClick={this.onTeamClick}
                onUserClick={this.onUserClick}
                onDogClick={this.onDogClick}
                onClickNav={onClickNav}
                rank={selectedMusherIdx + 1}
            >
                {/* {
                    "id": "305e720b-12bc-4be3-8244-47a2529c1a26",
                    "user": 679,
                    "Lp": "Finnmarkslpet 1200",
                    "bib": 32,
                    "firstname": "Thomas",
                    "lastname": "Wrner",
                    "cellphone": "48176051",
                    "email": "thomas.waerner@icloud.com",
                    "country": "NO",
                    "flag": "",
                    "team": 2278,
                    "startTime": "2019-03-08T18:32:00.000Z",
                    "maxStartTime": 1552071480000,
                    "startEpoch": 1552069920000,
                    "timeEqualization": 26,
                    "cpId": "f5834534-4398-49f5-a4ee-ad5c978afa07",
                    "cpDirection": "out",
                    "cpDogs": 13,
                    "cpTimestamp": "2019-03-13T00:08:00.000Z",
                    "cpSortOrder": 8
                } */}
                {/* [
                    {
                        "position": [],
                        "id": "d9f3d1b6-55e0-4185-b051-e96f1d4eaead",
                        "bib": 32,
                        "checkpoint": "f5834534-4398-49f5-a4ee-ad5c978afa07",
                        "direction": "out",
                        "dogs": 13,
                        "timestamp": "2019-03-13T00:08:00.000Z"
                    },
                    {
                        "position": [],
                        "id": "1503b852-3a61-4b91-8323-5e4f862c4399",
                        "bib": 32,
                        "checkpoint": "f5834534-4398-49f5-a4ee-ad5c978afa07",
                        "direction": "in",
                        "dogs": 13,
                        "timestamp": "2019-03-12T19:49:00.000Z"
                    }, */}
                    {/* {
                        "position":[],
                        "polygon":[],
                        "id":"fa914e06-c56c-4b5b-9719-2dc6ee12c05d",
                        "name":"Jergul",
                        "distanceFromStart":101,
                        "sortOrder":3,
                        "distanceFromPrev":101,
                        "estTimeToCpMinutes":484
                    } */}

                    {/* <h3>RaceLiveMusherResult</h3> */}
                    {/* <h5>{bib} {selectedMusher.lastname}</h5> */}
                    {/* <xmp>{JSON.stringify(team.imageHeader, null, 4)}</xmp> */}
                    {/* <xmp>{JSON.stringify(user, null, 4)}</xmp> */}
                    {/* <xmp>{JSON.stringify(image, null, 4)}</xmp> */}
                <div class='table-responsive'>
                    <table class='table table-striped table-sm'>
                        <thead>
                            <tr>
                                <th>Checkpoint</th>
                                <th class='text-center'>In</th>
                                <th class='text-center'>Out</th>
                                <th class='text-center'>Moving time</th>
                                <th class='text-center'>Avg speed</th>
                                <th class='text-center'>Total moving time</th>
                                <th class='text-center'>Rest</th>
                                <th class='text-center'>Total rest</th>
                                <th class='text-center'>% rest</th>
                                <th class='text-center'>Distance</th>
                                <th class='text-center'>Total distance</th>
                            </tr>
                        </thead>
                        <tbody>
                            {checkpoints && checkpoints.map((cp, cpIdx) => {
                                const now = Math.floor(new Date().getTime() / 1000, 10);
                                const resIn = results.slice().find(e => parseInt(e.bib, 10) == selectedMusher.bib && e.checkpoint === cp.id && e.direction === 'in') || {};
                                const resOut = results.slice().find(e => parseInt(e.bib, 10) == selectedMusher.bib && e.checkpoint === cp.id && e.direction === 'out') || {};
                                const resScratched = results.slice().find(e => parseInt(e.bib, 10) == selectedMusher.bib && e.checkpoint === cp.id && e.direction === 'scratched') || {};
                                let timeInCp = 0;
                                if (!cp.isStartLine && resIn && resOut && resIn.timestamp && resOut.timestamp) {
                                    timeInCp = mu.deltaSec(resIn.timestamp, resOut.timestamp);
                                }
                                let movingTime = 0;
                                if (lastResOut && resIn && lastResOut.timestamp && resIn.timestamp) {
                                    movingTime = mu.deltaSec(lastResOut.timestamp, resIn.timestamp);
                                }

                                let hasBeenInCp = false;
                                let avgSpeed = 0;
                                if (resIn && resIn.timestamp) {
                                    hasBeenInCp = true;
                                    avgSpeed = cp.distanceFromPrev / (movingTime / 3600);
                                }
                                totalDistance += cp.distanceFromPrev ? parseInt(cp.distanceFromPrev, 10) : 0;
                                totalMovingTime += movingTime ? movingTime : 0;
                                totalInCpTime += timeInCp ? timeInCp : 0;
                                totalTime += (movingTime ? movingTime : 0) + (timeInCp ? timeInCp : 0);
                                const percentRest = totalTime ? totalInCpTime / totalTime * 100 : 0;
                                const reqTotalRestInSec = currentClass.requiredRestMinutes * 60;
                                let line;
                                let estTimeOutClass = 'text-muted font-weight-lighter';
                                let totalRestClass = 'text-info';
                                if (hasBeenInCp) {
                                    let estTimeOut;
                                    let estRest;
                                    let restInCpSoFar;
                                    // if (cp.isStartLine) {
                                    //     estTimeIn = selectedMusher.startTime || currentClass.startDate;
                                    //     estRest = 0;
                                    if (!resOut || !resOut.timestamp) {
                                        if (cp.isStartLine) {
                                            estRest = 0;
                                        } else if (cp.timeEqualization) {
                                            if (currentClass.requiredRestMinutes && totalInCpTime < reqTotalRestInSec) {
                                                estRest = reqTotalRestInSec - totalInCpTime + selectedMusher.timeEqualization * 60;
                                                estTimeOutClass = 'text-warning font-weight-bold';
                                            }
                                            if (!estRest && cp.requiredRestMinutes && cp.timeEqualization && selectedMusher.timeEqualization) {
                                                estRest = cp.requiredRestMinutes * 60 + selectedMusher.timeEqualization * 60;
                                            }
                                        } else if (cp.requiredRestMinutes > 0) {
                                            estRest = cp.requiredRestMinutes * 60;
                                        } else {
                                            estRest = 180 * 60;
                                        }
                                        if (cp.isStartLine) {
                                            estTimeOut = mu.addDeltaSec(selectedMusher.startTime || currentClass.startDate, 0);
                                        } else {
                                            estTimeOut = mu.addDeltaSec(resIn.timestamp, estRest);
                                            restInCpSoFar = mu.deltaSec(resIn.timestamp, now);
                                        }
                                        lastEstTimeOut = estTimeOut;

                                        if (reqTotalRestInSec) {
                                            if ((totalInCpTime + restInCpSoFar) > (reqTotalRestInSec + (selectedMusher.timeEqualization * 60))) {
                                                totalRestClass = 'text-success';
                                            } else {
                                                totalRestClass = 'text-danger';
                                            }
                                        }
                                    }

                                    line = (<>
                                        <tr>
                                            <td><nobr>{cp.name}</nobr></td>
                                            <td class='text-center'>
                                                {!cp.isStartLine && <nobr>
                                                    <span class='text-muted mr-1'>{mu.formatDate(resIn.timestamp, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                    {mu.formatDate(resIn.timestamp, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                    {resIn.dogs > 0 && <><i class='fas fa-dog text-muted ml-2' /> {resIn.dogs}</>}
                                                </nobr>}
                                            </td>
                                            <td class='text-center'>
                                                {cp.isFinishLine ? <>
                                                    &nbsp;
                                                </> : <>
                                                    {resOut.timestamp && <nobr>
                                                        <span class='text-muted mr-1'>{mu.formatDate(resOut.timestamp, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                        {mu.formatDate(resOut.timestamp, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                        {resOut.dogs > 0 && <><i class='fas fa-dog text-muted ml-2' /> {resOut.dogs}</>}
                                                    </nobr>}
                                                    {!resScratched.timestamp && estTimeOut && <nobr class={`${estTimeOutClass}`}>
                                                        <span class='text-muted mr-1'>{mu.formatDate(estTimeOut, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                        {mu.formatDate(estTimeOut, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                    </nobr>}
                                                </>}
                                                {resScratched.timestamp && <nobr class='text-danger'>
                                                    <i class='fas fa-times' />
                                                    <span class='text-muted mr-1'>{mu.formatDate(resScratched.timestamp, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                    {mu.formatDate(resScratched.timestamp, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>}
                                            </td>
                                            <td class='text-center'>
                                                {!cp.isStartLine && <nobr>
                                                    {util.secToHms(movingTime, true)}
                                                </nobr>}
                                            </td>
                                            <td class='text-center'>
                                                {!cp.isStartLine && <nobr>
                                                    {util.format(avgSpeed || 0, 1)} <span class='text-muted font-weight-lighter'>km/t</span>
                                                </nobr>}
                                            </td>
                                            <td class='text-center'>
                                                {!cp.isStartLine && <nobr>
                                                    {util.secToHms(totalMovingTime, true)}
                                                </nobr>}
                                            </td>
                                            <td class='text-center'>
                                                {cp.isFinishLine || cp.isStartLine ? <>
                                                    &nbsp;
                                                </> : <>
                                                    {resScratched.timestamp ? <>
                                                        &nbsp;
                                                    </> : <>
                                                        {!resOut || !resOut.timestamp ? <>
                                                            <nobr class='text-info'>
                                                                {util.secToHms(restInCpSoFar, true)}
                                                            </nobr>
                                                        </> : <>
                                                            {!cp.isStartLine && <>
                                                                {estRest > 0 ? <>
                                                                    <nobr class='text-muted font-weight-lighter'>
                                                                        {util.secToHms(estRest, true)}
                                                                    </nobr>
                                                                </> : <>
                                                                    <nobr>
                                                                        {util.secToHms(timeInCp, true)}
                                                                    </nobr>
                                                                </>}
                                                            </>}
                                                        </>}
                                                    </>}
                                                </>}
                                            </td>
                                            <td class='text-center'>
                                                {cp.isFinishLine ? <>
                                                    <nobr>
                                                        {util.secToHms(totalInCpTime, true)}
                                                    </nobr>
                                                </> : <>
                                                    {resScratched.timestamp ? <>
                                                        &nbsp;
                                                    </> : <>
                                                        {!cp.isStartLine && <>
                                                            <nobr>
                                                                {restInCpSoFar ? <>
                                                                    <span class={totalRestClass}>
                                                                        {util.secToHms(totalInCpTime + restInCpSoFar, true)}
                                                                    </span> <span class='text-muted font-weight-lighter'>/ {util.secToHms(reqTotalRestInSec + (selectedMusher.timeEqualization * 60), true)}</span>
                                                                </> : <>
                                                                    {util.secToHms(totalInCpTime, true)}
                                                                </>}
                                                            </nobr>
                                                        </>}
                                                    </>}
                                                </>}
                                                {/* {cp.timeEqualization && <>
                                                    <br />
                                                    {currentClass.requiredRestMinutes}
                                                </>} */}
                                            </td>
                                            <td class='text-center'>
                                                {cp.isStartLine || resScratched.timestamp ? <>
                                                    &nbsp;
                                                </> : <>
                                                    {percentRest > 0 && <nobr>
                                                        {util.format(percentRest || 0, 0)} <span class='text-muted font-weight-lighter'>%</span>
                                                    </nobr>}
                                                </>}
                                            </td>
                                            <td class='text-center'>
                                                <nobr>
                                                    {cp.distanceFromPrev || 0} <span class='text-muted font-weight-lighter'>km</span>
                                                </nobr>
                                            </td>
                                            <td class='text-center'>
                                                <nobr>
                                                    {totalDistance} <span class='text-muted font-weight-lighter'>km</span>
                                                </nobr>
                                            </td>
                                        </tr>
                                    </>);
                                    if (resOut && resOut.timestamp) {
                                        lastEstTimeOut = resOut.timestamp;
                                        lastAvgSpeed = avgSpeed;
                                    }
                                } else {
                                    let estTimeIn;
                                    let estTimeOut;
                                    let estRest;
                                    let movingTimeSoFar;
                                    const distanceMeters = cp.distanceFromPrev * 1000;
                                    const avgSpeedMs = lastAvgSpeed / 3.6;
                                    let estTimeToNext = lastAvgSpeed ? (distanceMeters / avgSpeedMs / 60) : (cp.estTimeToCpMinutes || 0);
                                    if (cp.isStartLine) {
                                        estTimeIn = selectedMusher.startTime || currentClass.startDate;
                                        estRest = 0;
                                    } else if (lastEstTimeOut) {
                                        estTimeIn = mu.addDeltaSec(lastEstTimeOut, estTimeToNext * 60);
                                        // requiredRestMinutes: Number,
                                        // estTimeToCpMinutes: Number,
                                        // timeEqualization: Number,
                                        if (cp.isStartLine) {
                                            estRest = 0;
                                        } else if (cp.requiredRestMinutes && cp.timeEqualization && selectedMusher.timeEqualization) {
                                            estRest = cp.requiredRestMinutes * 60 + selectedMusher.timeEqualization * 60;
                                        } else if (cp.requiredRestMinutes > 0) {
                                            estRest = cp.requiredRestMinutes * 60;
                                        } else {
                                            estRest = (cp.estRestMinutes || 180) * 60;
                                        }
                                        estTimeOut = mu.addDeltaSec(estTimeIn, estRest);
                                        lastEstTimeOut = estTimeOut;
                                    }
                                    if (lastResOut && lastResOut.timestamp) {
                                        movingTimeSoFar = mu.deltaSec(lastResOut.timestamp, now);
                                    }

                                    avgSpeed = lastAvgSpeed || cp.distanceFromPrev / (estTimeToNext * 60 / 3600);
                                    line = (
                                        <tr>
                                            <td>{cp.name}</td>
                                            {lastResOut && lastResOut.timestamp ? <>
                                                <td class='text-center text-info font-weight-light'>
                                                    {!cp.isStartLine && estTimeIn && <nobr>
                                                        est:
                                                        <span class='text-muted mr-1 ml-1'>{mu.formatDate(estTimeIn, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                        {mu.formatDate(estTimeIn, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                    </nobr>}
                                                </td>
                                            </> : <>
                                                <td class='text-center text-muted font-weight-lighter'>
                                                    {!cp.isStartLine && estTimeIn && <nobr>
                                                        <span class='text-muted mr-1'>{mu.formatDate(estTimeIn, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                        {mu.formatDate(estTimeIn, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                    </nobr>}
                                                </td>
                                            </>}
                                            <td class='text-center text-muted font-weight-lighter'>
                                                {!cp.isFinishLine && estTimeOut && <nobr>
                                                    <span class='text-muted mr-1'>{mu.formatDate(estTimeOut, { locale, day: 'numeric', month: 'numeric' })}</span>
                                                    {mu.formatDate(estTimeOut, { locale, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>}
                                            </td>
                                            {lastResOut && lastResOut.timestamp ? <>
                                                <td class='text-center text-info'>
                                                    <nobr>
                                                        {util.secToHms(movingTimeSoFar, true)}
                                                    </nobr>
                                                </td>
                                            </> : <>
                                                <td class='text-center text-muted font-weight-lighter'>
                                                    {!cp.isStartLine && <nobr>
                                                        {util.secToHms(cp.estTimeToCpMinutes * 60, true)}
                                                    </nobr>}
                                                </td>
                                            </>}
                                            <td class='text-center text-muted font-weight-lighter'>
                                                {!cp.isStartLine && <nobr>
                                                    {util.format(avgSpeed || 0, 1)} <span class='text-muted font-weight-lighter'>km/t</span>
                                                </nobr>}
                                            </td>
                                            {lastResOut && lastResOut.timestamp ? <>
                                                <td class='text-center text-info'>
                                                    <nobr>
                                                        {util.secToHms(movingTimeSoFar + totalMovingTime, true)}
                                                    </nobr>
                                                </td>
                                            </> : <>
                                                <td class='text-center text-muted font-weight-lighter'>&nbsp;</td>
                                            </>}
                                            <td class='text-center text-muted font-weight-lighter'>
                                                {!cp.isStartLine && !cp.isFinishLine && <nobr>
                                                    {util.secToHms(estRest, true)}
                                                </nobr>}
                                            </td>
                                            <td class='text-center text-muted font-weight-lighter'>&nbsp;</td>
                                            <td class='text-center text-muted font-weight-lighter'>&nbsp;</td>
                                            <td class='text-center'>
                                                <nobr>
                                                    {cp.distanceFromPrev || 0} <span class='text-muted font-weight-lighter'>km</span>
                                                </nobr>
                                            </td>
                                            <td class='text-center text-muted font-weight-lighter'>&nbsp;</td>
                                            <td class='text-center text-muted font-weight-lighter'>&nbsp;</td>
                                        </tr>
                                    );
                                }
                                lastResOut = resOut;
                                lastResIn = resIn;
                                return line;
                            })}
                        </tbody>
                    </table>
                </div>

                {/* <xmp>{JSON.stringify(selectedMusher, null, 4)}</xmp> */}
                {/* <xmp>{JSON.stringify(currentClass, null, 4)}</xmp> */}
                {/* <xmp>{JSON.stringify(lastResults, null, 4)}</xmp> */}
            </MusherInfoHeader>
        </>);
    }
}

export default RaceLiveMusherResult;
